.buy-now {
  overflow: hidden;
  margin-top: 3rem;
  color: $white;

  &__container {
    display: flex;
    transition: transform 0.6s linear;
  }

  &__page {
    min-width: 100%;
    height: 100%;
    text-align: center;
    // margin: 0 auto;
    // padding-bottom: 100%;
    // background-size: fill;
    // background-position: center center;
    // background-repeat: no-repeat;
  }

  &__header {
    margin: 5.5rem 0 1rem;
  }

  &__desc {
    margin-bottom: 2rem;
  }
}

.buy-follower {
  width: 100%;
  display: flex;
  justify-content: center;
  // margin-bottom: 1rem;

  &__page {
    width: 33.3%;
    text-align: center;
    color: #545454;
    padding-bottom: 1rem;
    border-bottom: 1px solid #545454;

    &--prev {
      color: $white;
    }

    &--active {
      color: $contrast-color;
      border-bottom-color: $contrast-color;
    }
  }
}