body {
  font-family: "Inter", "Helvetica Neue", sans-serif;
  line-height: 1.5;
  color: $text-color;
  font-weight: 400;
}

h1,
h2,
h3 {
  font-weight: 900;
  color: $white;
  text-transform: uppercase;
}

h1 {
  font-size: 1.5rem;
}

h2 {
  font-size: 1.25rem;
}

h3 {
  font-size: 1rem;
}

.heading-highlight {
  font-weight: 400;
  color: $contrast-color;

  &--more {
    display: inline-block;
    height: 1rem;
    width: 8rem;
    // line-height: 1.5;
    background-image: url(../images/gaming.svg);
    background-size: contain;
    // background-position: center;
    background-repeat: no-repeat;
  }
}

.text {
  font-size: 1rem;

  &--small {
    font-size: 0.75rem;
  }
}
